<template>
  <div>
    Items Type:
    <select name="itemtype" v-model="value.items.type" @input="selectType($event.target.value)">
      <option v-for="type in types" :value="type" :key="type">{{type}}</option>
    </select>
    minItems: <input class="short-num" name="minimum" type="number" v-model.number="value.minimum" />
    maxItems: <input class="short-num" name="maximum" type="number" v-model.number="value.maximum" />
    uniqueItems: <input name="uniqueItems" type="checkbox" v-model="value.uniqueItems" />
    Format:
    <select name="format" v-model="value.format">
      <option v-for="f in formats" :value="f" :key="f">{{f}}</option>
    </select>
    <div class="option-form">
      <SchemaObject v-if="value.items.type == 'object'" :value="value.items"/>
      <SchemaString v-if="value.items.type == 'string'" :value="value.items"/>
      <SchemaNumber v-if="value.items.type == 'number'" :value="value.items"/>
      <SchemaArray v-if="value.items.type == 'array'" :value="value.items"/>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'

import SchemaString from './SchemaString'
import SchemaNumber from './SchemaNumber'
// import SchemaArray from './SchemaArray'
// import SchemaObject from './SchemaObject'

export default {
  name: 'SchemaArray',
  props: {
    value: Object
  },
  mounted() {
    this.value.items = this.value.items || {};
  },
  methods: {
    selectType(type) {
      if (type === 'array') {
        Vue.set(this.value.items, 'items', this.value.items.items || {})
      }
      if (type === 'object') {
        Vue.set(this.value.items, 'properties', this.value.items.properties || [])
        Vue.set(this.value.items, 'required', this.value.items.required || [])
      }
      this.value.items.type = type
    }
  },
  components: {
    SchemaString,
    SchemaNumber,
 //   SchemaArray,
 //   SchemaObject,
  },
  data() {
    return {
      types: ['string', 'number', 'array', 'object', 'boolean'],
      formats: ['', 'table', 'checkbox', 'select', 'tabs'],
    }
  },
}
</script>

<style scoped>
.option-form {
  padding-left: 25px;
  padding-top: 4px;
}

input.short-num {
  width: 50px;
}
</style>
